
html, body {
  width: 100%;
  height: 100%;
  margin: 0;
}
#app {
  width: 100%;
  height: 100%;
}
ul {
  padding: 0;
  margin: 0;
}

.layout {
  margin: 0;
  padding: 50px 0;
}
.wrapper_home {
  background: #F6F6F6;
  overflow: hidden;
}
.wrapper_home .content_home {
    width: 1000px;
    margin: 0 auto;
    background: #fff;
}

a {
  text-decoration: none;
  color: inherit;
}
.wrapper {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 50px;
  background: #FFFFFF;
  color: #8590A6;
  -webkit-box-shadow: 0 1px 3px rgba(26, 26, 26, 0.1);
          box-shadow: 0 1px 3px rgba(26, 26, 26, 0.1);
}
.wrapper .regin {
    position: absolute;
    top: 10px;
    right: 136px;
}
.wrapper .login {
    position: absolute;
    top: 10px;
    right: 190px;
    cursor: pointer;
}
.wrapper .handle {
    position: absolute;
    top: 60px;
    right: 172px;
    background: #fff;
    padding: 10px 20px;
    list-style: none;
    -webkit-box-shadow: rgba(26, 26, 26, 0.1) 0 0 10px;
            box-shadow: rgba(26, 26, 26, 0.1) 0 0 10px;
    cursor: pointer;
}
.wrapper .handle a {
      display: block;
      margin-top: 10px;
}

.wrapper_logout{
    margin-top:10px;
}

.article_layout {
  border-bottom: 1px solid #80797926;
  cursor: pointer;
}
.article_layout .article_title {
    font-size: 18px;
    font-weight: 600;
    color: #1A1A1A;
}
.article_layout .article_title:hover {
      color: #175199;
}

textarea{
    display: block
}
































.layout_login {
  width: 100%;
  height: 100%;
  background-color: #b8e5f8;
  background-image: url(/static/img/background.db29b0f.png);
  background-size: 100%;
  overflow: hidden;
}
.layout_login .wrapper_scroll {
    width: 432px;
    height: 100%;
    margin: 0 auto;
    overflow: hidden;
}
.layout_login .wrapper_scroll .control_scroll {
      width: 452px;
      height: 100%;
      overflow-y: visible;
      overflow-x: hidden;
}
.layout_login .wrapper_scroll .control_scroll .blank {
        width: 432px;
        height: 80px;
}
.layout_login .wrapper_scroll .control_scroll .login {
        width: 432px;
        background: #fff;
        border-radius: 5px 5px 0 0;
        padding: 0 40px 40px 40px;
        -webkit-box-sizing: border-box;
                box-sizing: border-box;
        position: relative;
}
.layout_login .wrapper_scroll .control_scroll .login .logo {
          width: 100%;
          height: 150px;
}
.layout_login .wrapper_scroll .control_scroll .login .inputType {
          border: none;
          width: 100%;
          height: 50px;
          line-height: 50px;
          padding: 10 0px;
          border-bottom: 1px solid #EBEBEB;
          font-size: 16px;
}
.layout_login .wrapper_scroll .control_scroll .login .inputType:focus {
            outline: none;
}
.layout_login .wrapper_scroll .control_scroll .login .errType {
          border: none;
          width: 100%;
          height: 50px;
          line-height: 50px;
          padding: 10 0px;
          border-bottom: 1px solid #EBEBEB;
          font-size: 16px;
          color: red;
}
.layout_login .wrapper_scroll .control_scroll .login .errType:focus {
            outline: none;
}
.layout_login .wrapper_scroll .control_scroll .login .identify {
          padding: 20px 0;
}
.layout_login .wrapper_scroll .control_scroll .login .login_btn {
          width: 100%;
          height: 36px;
          line-height: 36px;
          background-color: #0084FF;
          border-radius: 5px;
          color: #fff;
          text-align: center;
          cursor: pointer;
}
.layout_login .wrapper_scroll .control_scroll .login .fontColor::-webkit-input-placeholder {
          color: red;
}
.layout_login .wrapper_scroll .control_scroll .login .fontColor::-moz-input-placeholder {
          color: red;
}
.layout_login .wrapper_scroll .control_scroll .login .fontColor::-ms-input-placeholder {
          color: red;
}
.layout_login .wrapper_scroll .control_scroll .login .user_err {
          position: absolute;
          top: 166px;
          right: 41px;
          color: red;
          background: #fff;
          padding: 0 10px;
}
.layout_login .wrapper_scroll .control_scroll .login .pass_err {
          position: absolute;
          top: 217px;
          right: 41px;
          color: red;
          background: #fff;
          padding: 0 10px;
}
.layout_login .wrapper_scroll .control_scroll .jump {
        width: 100%;
        background-color: #F6F6F6;
        border-top: 1px solid #EBEBEB;
        text-align: center;
        height: 58px;
        line-height: 58px;
        font-size: 16px;
}
.layout_login .wrapper_scroll .control_scroll .jump a {
          text-decoration: none;
          color: #175199;
          cursor: pointer;
}

