.layout {
  margin: 0;
  padding: 50px 0;
  background: #F6F6F6;
}
.layout .showArticle_wrapper {
    width: 1000px;
    margin: 0 auto;
    padding: 20px;
    background: #fff;
    margin-top: 20px;
}
.layout .showArticle_user {
    font-weight: 600;
    font-size: 15px;
}
.layout .showArticle_time {
    font-size: 14px;
    color: #8590A6;
}
.layout .showArticle_navigation {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
.layout .showArticle_navigation .attitude {
      padding: 0 10px;
      background: rgba(0, 132, 255, 0.1);
      color: #0084FF;
      height: 32px;
      line-height: 32px;
}
.layout .showArticle_navigation .disagree {
      margin-left: 4px;
}
.layout .showArticle_navigation .comment_number {
      margin-left: 24px;
      height: 32px;
      line-height: 32px;
      font-size: 14px;
      color: #8590A6;
      cursor: pointer;
}
.layout .showArticle_navigation .comment_number:hover {
        color: #76839b;
}
.layout .showArticle_container {
    margin-top: 12px;
    border: 1px solid #EBEBEB;
    -webkit-box-shadow: 0 1px 3px rgba(26, 26, 26, 0.1);
            box-shadow: 0 1px 3px rgba(26, 26, 26, 0.1);
    border-radius: 4px;
}
.layout .showArticle_container .comment_input {
      -web-kit-appearance: none;
      -moz-appearance: none;
      padding: 4px 10px;
      width: 200px;
      height: 20px;
      border: 1px solid #EBEBEB;
      border-radius: 3px;
      background: #F6F6F6;
      font-size: 16px;
      color: #8590A6;
}
.layout .showArticle_container .comment_send {
      padding: 0 12px;
      height: 30px;
      line-height: 30px;
      color: #FFFFFF;
      background-color: #0084FF;
      border-style: none;
      border-radius: 4px;
      font-size: 14px;
      margin-left: 10px;
}
.layout .showArticle_container .comment_send:hover {
        background-color: #1880e0;
}
.layout .showArticle_container .showArticle_topBar {
      padding: 0 20px;
      height: 50px;
      line-height: 50px;
      font-size: 15px;
      font-weight: 600;
      border-bottom: 1px solid #F6F6F6;
}
.layout .showArticle_container .comment_list {
      padding: 12px 20px 10px;
}
.layout .showArticle_container .comment_list .comment_content {
        margin: 0;
        padding: 10px 0;
        border-bottom: 1px solid #EBEBEB;
}
.layout .showArticle_container .comment_list .comment_content p {
          margin: 0;
}
.layout .showArticle_container .comment_list .comment_content_2 {
        padding: 10px 0 10px 20px;
}
.layout .showArticle_container .comment_list .comment_content_2 p {
          margin: 10px 0 0 0;
}
.layout .showArticle_container .comment_list .comment_content_2 .comment_layout_2 {
          border-bottom: 1px solid #EBEBEB;
          padding-bottom: 10px;
}
.layout .showArticle_container .comment_list .comment_content_2 .comment_layout_2 .comment_layout_3 {
            padding: 10px 0 10px 30px;
}
.layout .showArticle_container .comment_list .comment_content_2 .comment_layout_2 .comment_layout_3 p {
              padding-bottom: 10px;
              border-bottom: 1px solid #EBEBEB;
}
.layout .showArticle_container .comment_list .resBar {
        margin-top: 6px;
}
.layout .showArticle_container .comment_list .resBar span {
          cursor: pointer;
}
.layout .showArticle_container .comment_list .class {
        display: inline-block;
        padding: 4px 10px;
        background-color: #0084FF;
        color: #fff;
        font-size: 14px;
        margin-top: 10px;
        border-radius: 4px;
        cursor: pointer;
}
.layout .showArticle_container .addComment {
      padding: 12px 20px 10px;
}
